export class FileZipListViewModel {
  /// <summary>
///  壓縮檔序號
/// <summary>
public zip_seq: number;
/// <summary>
///  壓縮檔名稱
/// <summary>
public zip_filename: string;
/// <summary>
///  轉檔群組
/// <summary>
public exec_group: string;
/// <summary>
///  參數欄位群組
/// <summary>
public parameter_column_group: string;
/// <summary>
///  啟用註記
/// <summary>
public is_active: boolean;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;
/// <summary>
  ///  啟用註記說明
  /// <summary>
  public is_active_desc: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color_right: string;

  /// <summary>
  ///  是否有設定密碼
  /// </summary>
  public is_pwd: boolean;

  /// <summary>
  ///  密碼顏色
  /// </summary>
  public pwd_color: string;


  /// <summary>
  ///  回圈密碼筆數
  /// </summary>
  public loop_pwd_cnt: number;

  /// <summary>
  ///   回圈密碼顏色
  /// </summary>
  public loop_pwd_color: string;

}



