import { SingleModelResponse } from "./SingleModelResponse";
import { FileZipListViewModel } from "../ViewModel/FileZipListViewModel";

export class FileZipListResponse extends SingleModelResponse<
  FileZipListViewModel
> {
  Model: FileZipListViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new FileZipListViewModel();
      this.Model.zip_seq = obj.Model.zip_seq;
      this.Model.zip_filename = obj.Model.zip_filename;
      this.Model.exec_group = obj.Model.exec_group;
      this.Model.parameter_column_group =
        (obj && obj.Model.parameter_column_group) || null;
      this.Model.is_active = obj.Model.is_active;
      this.Model.creator = obj.Model.creator;
      this.Model.create_time = obj.Model.create_time;
      this.Model.modifier = obj.Model.modifier;
      this.Model.last_update_time = obj.Model.last_update_time;

      this.Model.is_active_desc = obj.Model.is_active_desc;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
          this.Model.is_active_color = "active";
      }
      this.Model.is_pwd = obj.Model.is_pwd;

      if (this.Model.is_pwd === true) {
        this.Model.pwd_color = "danger";
      } else this.Model.pwd_color = "light";

      this.Model.loop_pwd_cnt = obj.Model.loop_pwd_cnt;
      if (this.Model.loop_pwd_cnt > 0) {
        this.Model.loop_pwd_color = "danger";
      } else this.Model.loop_pwd_color = "light";
    }
  }
}



