import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileZipListListResponse } from "../../Model/Response/FileZipListListResponse";
import { FileZipListResponse } from "../../Model/Response/FileZipListResponse";
import { FileZipListViewModel } from '../../Model/ViewModel/FileZipListViewModel';
import {StringResponse} from '../../Model/Response/StringResponse';
import {NumberResponse} from '../../Model/Response/NumberResponse';
/*
  Generated class for the FileZipListServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileZipListServicesProvider {
  private FileZipLists: FileZipListListResponse;
  private FileZipList: FileZipListResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
     this.ctl = "FileZipList";
    console.log("Hello FileZipListServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,order_type:boolean) {
    var sub_url: string;

      sub_url = "FileZipList?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);

    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileZipLists = new FileZipListListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileZipLists;
    });
  }



  public PostPutAsync(FileZipListViewModel:FileZipListViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileZipList";
    else
    sub_url = "FileZipList/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileZipListViewModel.zip_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileZipListViewModel,mode).map(
      item => {
        this.FileZipList = new FileZipListResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileZipList;
      }
    );
  }

  public DeleteAsync(zip_seq:string) {
    var sub_url: string;
    sub_url = "FileZipList/{1}";
    sub_url = String.Format(sub_url, this.ctl,zip_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: ""
        });
      }
    );
  }

  public GetMaxZipSeqAsync(exec_group:string) {
    var sub_url: string;
    sub_url = "FileZipList/get_max_zip_seq?exec_group={1}";
    sub_url = String.Format(sub_url, this.ctl,exec_group);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public GetSqlAsync(zip_seq:string,method:string) {
    var sub_url: string;
    sub_url = "FileZipList/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,zip_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



