import { FileZipListServicesProvider } from "../../providers/file-zip-list-services/file-zip-list-services";
import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileZipListViewModel } from "../../Model/ViewModel/FileZipListViewModel";

/**
 * Generated class for the FileZipListModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-zip-list-add-edit-modal",
  templateUrl: "file-zip-list-add-edit-modal.html"
})
export class FileZipListAddEditModalPage {
  item: FileZipListViewModel;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  FileZipList_type: string;
  change_mode:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private FileZipListServices: FileZipListServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController
  ) {

    this.mode = navParams.data.mode;
    this.change_mode=navParams.data.change_mode;
    this.item = new FileZipListViewModel();
    this.item.zip_seq=navParams.data.item.zip_seq;
    this.item.zip_filename=navParams.data.item.zip_filename;
    this.item.exec_group=navParams.data.item.exec_group;
    this.item.parameter_column_group=navParams.data.item.parameter_column_group;
    this.item.is_active=navParams.data.item.is_active;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;

    if (this.change_mode==="c" ) {
      this.get_max_zip_seq();
    }
    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title = "壓縮檔案設定";
  }

  SelectParameterGroup() {
    let modal = this.modalCtrl.create("ParameterGroupSelectModelPage", {
      select_parameter_column_group: this.item.parameter_column_group,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.parameter_column_group = select_data.parameter_column_group;
    });

    modal.present();
  }
  openNavParameterColumnPage(item) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.navCtrl.push("ParameterColumnPage", { item: item });
    });
    this.global.dismissLoading();
  }

  get_max_zip_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileZipListServices.GetMaxZipSeqAsync(
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.zip_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }



  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileZipListModalPage");
  }
}



